export default {
    data(){
        return{
            userInfor:null,
            privateInfor:null
        }
    },
    mounted(){
    },
    methods:{
        getUserInfor(){
            this.$get(`private/user/${this.userInfor._id}`)
                .then(res =>{
                    this.privateInfor = res.data
                    this.$auth.fetchUser();
                })
                .catch(err => {
                    console.log(err)
                })
        },
        passIDInfor(item){
            this.userInfor = item
            this.privateInfor = null
        },
        show() {
            this.$refs.modalLien.showModal();
        },
        hide(){
            this.$refs.modalLien.hideModal()
        },
    },
}
